@use "../config/colors" as color;
@import "../config/mixins";

.basket-modal {
  position: fixed;
  bottom: 0;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: center;
  display: none;

  min-width: 35%;
  max-height: 50%;
  margin: 10px;

  overflow-y: auto;
  overflow-x: hidden;
  border: 0.5px solid rgb(56, 56, 56);
  border-radius: 20px;
  background-color: color.$bg-secondary-color;

  &--active {
    display: block;
  }
  &__content {
    position: relative;
    padding: 15px;
    color: color.$dark-text-color;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 20px;
  }
  &__title {
    margin-right: 30px;
    @include text-style(500, 24px, 20px);
    color: color.$dark-text-color;
  }
  &__clear-all-button {
    padding: 10px;

    @include text-style(300, 18px, 20px);

    border: none;
    border-radius: 10px;
    color: color.$ligth-text-color;
    opacity: 0.9;
    background-color: color.$btn-bg-color;

    transition: 0.3s linear;

    cursor: pointer;

    &:hover {
      opacity: 1;
      color: color.$light-grey-text-color;
    }
  }
  &__container {
    margin-bottom: 20px;
  }
  .basket-item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-bottom: 15px;

    &__title {
      max-width: 250px;
      margin-right: 10px;
      @include text-style(300, 20px, 20px);
    }
    &__increase,
    &__reduce,
    &__remove {
      padding: 5px 10px;
      margin: 0px 5px;

      @include text-style(300, 18px, 20px);

      border: none;
      border-radius: 10px;
      color: color.$ligth-text-color;
      opacity: 0.9;
      background-color: color.$btn-bg-color;

      transition: 0.3s linear;

      cursor: pointer;

      &:hover {
        opacity: 1;
        color: color.$light-grey-text-color;
      }
    }
    &__price {
      @include text-style(500, 18px, 20px);

      margin: 0px 5px;
    }
    &__amount {
      @include text-style(300, 18px, 20px);

      margin: 0px 5px;
    }
  }
  &__price {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price-text {
    @include text-style(500, 22px, 20px);
    color: color.$dark-text-color;
  }
  &__price-sum {
    margin: 0px 5px;
    @include text-style(500, 22px, 20px);
    color: color.$dark-text-color;
  }
  @include sm {
    width: 100%;
    min-height: 40%;
    max-height: 60%;
    margin: 0px;
  }

  @include md {
    max-width: 50%;
    min-height: auto;
    margin: 10px;
  }

  @include lg {
    max-width: 40%;
    margin: 10px;
  }

  @include xl {
    max-width: 40%;
    margin: 10px;
  }
}
.display-block {
  display: block;
}
